$floating-label-height: add(2.5rem, $input-height-border) !default;
$floating-label-line-height: 1.25 !default;

@mixin input-styles() {
  width: 100%;
  line-height: $floating-label-line-height;
}

@mixin outline-input-styles() {
  @include input-styles();
  border: solid 1px;
  border-color: $input-border-color;
  border-top-color: transparent;
  background-color: transparent;
  box-shadow: none;
  transition: border 0.2s, box-shadow 0.2s;
  padding: $input-padding-y $input-padding-x;
  @include border-radius($input-border-radius, 0);
}

@mixin label-styles() {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  border-color: $input-border-color;
  width: 100%;
  max-height: 100%;
  font-size: 75%;
  line-height: 1;
  cursor: text;
  transition: color 0.2s, font-size 0.2s, line-height 0.2s;
}

@mixin outline-input-focus() {
  border-color: $input-focus-border-color;
  border-top-color: transparent;
  box-shadow: inset 1px 0 $input-focus-border-color, inset -1px 0 $input-focus-border-color, inset 0 -1px $input-focus-border-color;
  outline: none;
}

@mixin outline-label-focus-before() {
  color: $component-active-bg;
  &::before, &::after {
    border-top-color: $input-focus-border-color;
    box-shadow: inset 0 1px $input-focus-border-color;
  }
}

@mixin outline-placeholder-shown() {
  border-top-color: $input-border-color;
  + label {
    font-size: inherit;
    line-height: 3;

    &::before, &::after {
      border-top-color: transparent;
    }
  }
}

@mixin outline-label-styles-before {
  content: "";
  display: block;
  box-sizing: border-box;
  margin-top: 6px;
  border-top: solid 1px;
  border-top-color: $input-border-color;
  min-width: 10px;
  height: 8px;
  pointer-events: none;
  box-shadow: inset 0 1px transparent;
  transition: border-color 0.2s, box-shadow 0.2s;
}

@mixin filled-input-styles() {
  @include input-styles();
  border: none;
  border-bottom: 1px solid $input-border-color;
  border-radius: 0;
  border-top-left-radius: 0.25rem;
  border-top-right-radius: 0.25rem;
  background-color: #f5f5f5;
  padding: $form-floating-padding-y $form-floating-padding-x;
}

@mixin filled-input-focus {
  outline: 0;
  border-bottom: 1px solid $input-focus-border-color;
  box-shadow: none;
  background-color: #eee;
}

.floating-label {
  position: relative;

  &:not(.filled) {
    padding-top: 6px;
    margin-top: -6px;

    > input,
    > select {
      height: $floating-label-height;
      min-height: $floating-label-height;
    }

    > input,
    > select,
    > textarea {
      @include outline-input-styles();

      + label {
        @include label-styles();

        &::before, &::after {
          @include outline-label-styles-before();
        }

        &::before {
          margin-right: 4px;
          border-left: solid 1px transparent;
          border-radius: 4px 0;
        }

        &::after {
          flex-grow: 1;
          margin-left: 4px;
          border-right: solid 1px transparent;
          border-radius: 0 4px;
        }
      }

      &:focus {
        @include outline-input-focus();

        + label {
          @include outline-label-focus-before();
        }
      }

      &:not(:focus):placeholder-shown {
        @include outline-placeholder-shown();
      }
    }
  }

  &.filled {
    > input,
    > textarea,
    > select {
      @include filled-input-styles();

      &:focus {
        @include filled-input-focus();
      }

      &::-moz-placeholder {
        color: transparent;
      }

      &::placeholder {
        color: transparent;
      }

      &:focus,
      &:not(:placeholder-shown) {
        //padding-top: 1.625rem;
        //padding-bottom: 0.625rem;
        border-bottom: 1px solid $input-focus-border-color;
      }
    }

    label {
      position: absolute;
      display: flex;
      align-items: center;
      top: 0;
      left: 0;
      height: 100%;
      pointer-events: none;
      border: 1px solid transparent;
      transform-origin: 0 0;
      transition: color 0.15s ease-in-out, transform 0.15s ease-in-out;
      margin-bottom: 0;

      &::before {
        content: " "; // Adding a space as a fallback for browsers that don't support empty content
      }
    }

    .form-control:focus ~ label,
    .form-control:not(:placeholder-shown) ~ label {
      transform: scale(0.85) translateY(-0.75rem) translateX(0.15rem);
      color: $component-active-bg;
    }
  }

  > input::placeholder,
  > select::placeholder,
  > textarea::placeholder {
    color: transparent;
  }
}


